import React from 'react';
import { Link } from 'react-router-dom';

import DocBuilder from '../../../components/DocBuilder';
import guideReferenceIcon from '../../../assets/img/guide_reference.svg';
import request from '../../../documentation/documentation/BankPayment_3p-request';
import response from '../../../documentation/documentation/BankPayment_3p-response';
import {guideReferenceURL} from '../../../env';
import Breadcrumbs from "../../../components/Breadcrumbs";

class BankPayment extends React.Component {

    constructor(props, context) {
        super(props, context);

        this.state = {
            showDocumentation: true
        };
    }

    componentDidMount(){
        if(this.props.showDocumentation === false){
            this.setState({
                showDocumentation: this.props.showDocumentation
            })
        }
    }

    render() {
        return (
            <div className="container-fluid example-selector-container">
                <div className="row">
                    <div className="col-12">
                        {
                            this.state.showDocumentation &&
                                <div>
                                    <Breadcrumbs/>
                                    <p>
                                        This transaction allows a client to make bill payments through a debit on his own bank account.
                                        The payment is not fulfilled until the client confirms it, so it is not actually online, but deferred.

                                        The following method generates a pending payment for a specific bill on the clients bank account.
                                    </p>
                                    <div className="alert alert-info">
                                        <div className="row">
                                            <div className="col-12">
                                                <img src={guideReferenceIcon} alt="Locked" style={{height: '100px'}}/>
                                                <span>
                                            For more information please go to&nbsp;
                                                    <Link to={`${guideReferenceURL}/reference/simple/bank`}>
                                              Guide Reference: Simple Checkout
                                            </Link>
                                        </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        }
                        <DocBuilder showEndpoint={true} data={{request:request, response: response}} endpoint='BankPayment_3p' showDocumentation={this.state.showDocumentation} />
                    </div>
                </div>
            </div>
        );
    }
}

export default BankPayment;