import React from 'react';
import { Link } from 'react-router-dom';

import DocBuilder from '../../../../components/DocBuilder';
import guideReferenceIcon from '../../../../assets/img/guide_reference.svg';
import request from '../../../../documentation/documentation/PayOnLine_2p-request';
import response from '../../../../documentation/documentation/PayOnLine_2p-response';
import {guideReferenceURL} from '../../../../env';
import Breadcrumbs from "../../../../components/Breadcrumbs";

class Sale extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            showDocumentation: true
        };
    }

    componentDidMount(){
        if(this.props.showDocumentation === false){
            this.setState({
                showDocumentation: this.props.showDocumentation
            })
        }
    }

    render() {
        return (
            <div className="container-fluid example-selector-container">
                <div className="row">
                    <div className="col-12">
                        { this.state.showDocumentation &&
                            <div>
                                <Breadcrumbs/>
                                <p>
                                    <br/>
                                    The purchase transaction performs an authorization with automatic settlement.
                                    <br/>
                                </p>
                                <div className="alert alert-info">
                                    <div className="row">
                                        <div className="col-12">
                                            <img src={guideReferenceIcon} alt="Locked" style={{height: '100px'}}/>
                                            <span>
                                            For more information please go to&nbsp;
                                                <Link to={`${guideReferenceURL}/reference/directPayment`}>
                                              Guide Reference: Direct Payment
                                            </Link>
                                        </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        }
                        <DocBuilder
                            showEndpoint={true}
                            data={{request:request, response: response}}
                            endpoint='PayOnline_2p'
                            onlyFiles={this.props.onlyFiles}
                            showDocumentation={this.state.showDocumentation} />
                    </div>
                </div>
            </div>
        );
    }
}

export default Sale;